.ESuper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(../../images/bgteam.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

.EContent {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 80%;
  margin-top: 100px;
  margin-bottom: 100px;
}

.ETitle h1 {
  font-size: 52px;
  font-weight: bold;
  color: var(--bgcolor2);
}

.ETitle span {
  color: var(--color1);
}

.ECards {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.ECardContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-basis: 24%;
}

.ECC-alt1 {
  flex-basis: 40%;
  display: flex;
  justify-content: end;
  align-items: center;
  /* padding-right: 50px; */

  /* border: solid red 1px; */
}

.ECC-alt2 {
  flex-basis: 40%;
  display: flex;
  justify-content: start;
  align-items: center;
  /* padding-left: 50px; */

  /* border: solid rgb(212, 0, 255) 1px; */
}

.ECard {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  background-image: url(../../images/bgteamcard.png);
  width: 250px;
  height: 444px;
  border-radius: 10px;
  box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.5);
  margin: 20px;

  /* border: solid red 1px; */
}

.ECard img {
  position: absolute;
  top: 40px;
}

.ECard p {
  position: absolute;
  top: 300px;
  text-align: center;
  font-weight: bold;
}
